.projects-cont{
	scroll-behavior: smooth;
	position: relative;
    background: linear-gradient(white,rgba(255,244,232,1)) ;
	background: #ffeee0;
	width: 100vw;
    .anchor{
    	z-index: 5;
    	font-size: clamp(0.8rem,1.8vw,1rem);
    	width: 5em;
    	height: 5em;
    	border-radius: 1000px;
    	// background-color: #ffeedf;
    	box-shadow: rgba(0, 0, 0, 0.2) 0px 60px 40px -7px;
    	position: sticky;
    	top: 2em;
    	left: 2em;
    	display: flex;
    	justify-content: center;
    	align-items: center;
    	margin-bottom: 14em;
    	margin-top: 28em;
    	img{
    		height: 22%;
    	}
    }
	.hero-header{
		font-family: 'Mialgor';
		color: #130000;
		width: 100vw;
		padding-top: calc(38rem - 16vw);
		// padding-top: 48vh;
		height: 34vh;
		position: sticky;
		top: 0vh;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size:clamp(4rem,20vw,28rem) ;
	}
}

