.projects-page{
	  // position: relative;
	  // width:100%;
	//   .items-cont{
	//   position: sticky;
	//   z-index: 4;
	//   top: 0;
	//   display: flex;
	//   flex-direction: column;
	//   width: 100%;
	//   align-items: center;
	//   transition: margin 0.4s ease-out;
	  // height: 100vh;
	  // overflow-y: scroll;
	  // scroll-behavior: smooth;
	  // scroll-snap-type: y mandatory;
	  position: relative;
	  .item-cont{
		  // scroll-snap-align: start;
		  width: 100vw;
		  height: 100vh !important;
		  display: flex;
		  justify-content: center;
		  
		  .item{
			 
			  height:88vh;
			  width: 90vw;
			  max-width: 64em;
			  position: relative;
			  font-size: clamp(0.5rem,2.2vw,0.9rem);
			  .border-layer{
				  width: 100%;
				  height: 100%;
				  border: 4px solid white;
				  position: absolute;
				  z-index: 5;
				  top: 0;
			  }
			  .paper-layer{
				  position: absolute;
				  top: 0;
				  z-index: 1;
				  width: 100%;
				  height: 100%;
				  overflow: hidden;
				  mix-blend-mode: multiply;
				  opacity: 0.5;
				  transition: 0.8s linear opacity;
				  img{
					  height:100%;
				  }
			  }
			  .blur-layer{
				  position: absolute;
				  top: 0;
				  z-index: -1;
				  width: 100%;
				  height: 100%;
				  display: flex;
				  align-items: center;
				  transition: opacity ease-in-out 0.5s;
				  .child{
					  width: 110%;
				      height: 110%;
					  transition: filter ease-in-out 1s;
					  filter: blur(300px);
					  background: rgba(255, 242, 217, 0.86);

				  }
			  }
			  .content-layer{
				  
				//   box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
				  height: 100%;
				  width: 100%;
				  transition-delay: 1s;
				  transition: box-shadow 2s ease-in;
				  // background: rgba(255, 242, 217, 0.86);
				  color: rgba(54, 29, 29, 1);
                 
				  background-color: #ffeee0;
				  background: rgba(255, 243, 219, 0.76);
				  overflow: hidden;
				  display: flex;
				  flex-direction: column;
				  justify-content: flex-start;
				  align-items: center;
				  .img-cont , .header , .details{
					  width: 85%;
				  }
				  .img-cont{
					  width:85%;
					  aspect-ratio: 2/1.8;
					  margin-top: 10%;
					  
					  overflow: hidden;
					  position: relative;
					  @media (width<400px) {
						aspect-ratio: 2/2.2;
					  
					}
                     @media (width>600px) {
						aspect-ratio: 4/3;
					 }
					 @media (width>800px) {
						aspect-ratio: 2/1;
						margin-top: 8%;
					 }
					 
					  img{
						  width: 100%;
						  
						  @media (width>400px) {
							position: absolute;
							top: -20%;
						  }
						  @media (width>600px) {
							position: absolute;
							top: -30%;
						 }
						 @media (width>800px) {
							position: absolute;
							top: -60%;
						 }
					  }
				  }
				  .header{
					  text-align: center;
					  font-family: Rebeqa-ExtraBold;
					  font-size: 50px;
					  font-style: normal;
					  font-weight: 900;
					  display: flex;
					  padding-top: 0.6em;
					  padding-bottom:0.1em;
					  justify-content: space-between;
					  align-items: flex-end;
					  .release{
						  font-size: 0.5em !important;
					  }
				  }
				  .details{
					  color: #000;
					  font-family: Emberly-Black;
					  font-size: 17px;
					  font-style: normal;
					  font-weight: 400;
					  line-height: normal;
					  margin-bottom: 2em;
				  }
				  .tools-cont{
					   width: 85%;
					   display: flex;
					   height: 10vh;
					   .names{
						color:black;
						font-family: Rebeqa-Bold;
						font-size: 1.4rem;
						font-weight: bolder;
						display: flex;
						flex-direction: column;
						justify-content: space-between;
					   }
					   .bars{
						width: 100%;
						display: flex;
						flex-direction: column;
						justify-content: space-between;
						margin-bottom: 0.5rem;
						margin-left: 2.4em;
						   .bar{
							margin-top: 0.3rem;
							height: 1.2vh;
							border-radius: 2px;
							background-color: black;
						   }
					   }
				  }
				  
			  }

		  }
		  
	  }
	  

  
}


.swiper-slide-active{
    .item-cont{
		.item{
			.blur-layer{
				.child{
					filter: blur(15px);
				}
			}
			.paper-layer{
				opacity: 1;
			}
			.content-layer{
				// box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
			}
		}
	}
}